@import '../App.scss';

table {
    margin: 0 auto;
    border: 1px solid $base-color1;
    text-align: center;
    width: 90%;
    border-collapse: collapse; border-spacing: 0; /* cellspacing */
}
thead {
    th {
        padding: 5px 10px;
        border: 1px solid $base-color1;
        background-color: $base-color3;
        color: $light-border;
    }
}
tbody {
    tr {
        margin: 10px;

        &:hover {
            background-color: $light-border;
        }
        td {
            padding: 5px 0;
            border: 1px solid $base-color1;
            
        }
    }
}
#exports {
    margin: 0 auto;
    width: 90%;
    display: flex;
    justify-content: right;
    button {
        margin: 20px 10px 10px 10px;
        padding: 10px 20px;
        background-color: $base-color1;
        color: $light-border;
        border-radius: 4px;
        border: none;
        transition: 0.2s;
        &:hover {
            cursor: pointer;
            // background-color: $light-border;
            opacity: 70%;
            // filter: brightness(0%);
        }
    }
}